<template>
  <div>
    <h2 class="text-center mt-5"></h2>
    <hr />
    <div class="row align-items-center p-2" dir="rtl">
      <div class="col-md-3">
        <div class="form-group">
          <label style="font-size: 0.7rem" for=""
            >        </label
          >
          <input @input="search" class="form-control" type="text" placeholder="search" />
        </div>
      </div>
      <div class="col-md-3">
        <div class="form-group">
          <label for=""> </label>
          <select @change="filterUsers" class="form-control" v-model="stage">
            <option selected value="all"> </option>
            <option selected value="1">  </option>
            <option selected value="2">  </option>
            <option selected value="3">  </option>
            <option selected value="4">  </option>
            <option selected value="5">  </option>
            <option selected value="6">  </option>
          </select>
        </div>
      </div>
      <div class="col-md-3">
        <div class="form-group">
          <label for="">  </label>
          <select @change="filterUsers" v-model="role" class="form-control">
            <option value="0"></option>
            <option value="1"></option>
          </select>
        </div>
      </div>

      <div class="col-md-3">
        <div class="form-group">
          <label for="">     </label>
          <select @change="filterUsers" v-model="confirmed" class="form-control">
            <option value="all"></option>
            <option value="1"> </option>
            <option value="0">  </option>
          </select>
        </div>
      </div>

      <div class="col-md-3">
        <div class="form-group">
          <label for=""> </label>
          <select @change="filterUsers" v-model="registered" class="form-control">
            <option value="all"></option>
            <option :value="false">  </option>
            <option :value="true">     </option>
          </select>
        </div>
      </div>
      <div class="col-md-3">
        <button @click="unconfirmAll" class="btn btn-danger">Unconfirm all</button>
      </div>
      <div class="col-md-3">
        <button @click="allowAll" class="btn btn-success">Allow all</button>
      </div>
    </div>
    <v-data-table :headers="headers" :items="modifiedUsers" class="elevation-1">
      <template v-slot:item.controls="row">
        <div class="controls">
          <button
            v-if="row.item.role == 'Student'"
            @click="() => $router.push('/dashboard/studentFile/' + row.item._id)"
            type="button"
            class="btn btn-outline-danger btn-xs"
          >
            <i class="fa fa-eye" aria-hidden="true"></i>
          </button>
          <button
            v-if="row.item.role == 'Student'"
            @click="() => remove(row.item._id)"
            type="button"
            class="btn btn-outline-danger btn-xs"
          >
            <i class="fa fa-trash" aria-hidden="true"></i>
          </button>
          <button
            @click="() => toggleRole(row.item._id)"
            type="button"
            class="btn btn-outline-danger btn-xs"
          >
            <i class="fa fa-user" aria-hidden="true"></i>
          </button>
          <button
            -0
            @click="() => confirmUser(row.item._id)"
            type="button"
            class="btn btn-outline-danger btn-xs"
          >
            <i class="fa fa-check" aria-hidden="true"></i>
          </button>
          <button
            @click="() => $router.push('/update/' + row.item._id)"
            type="button"
            class="btn btn-outline-danger btn-xs"
          >
            <i class="fa fa-pencil" aria-hidden="true"></i>
          </button>

          <button
            :disabled="!row.item.registered"
            @click="allowRegister(row.item._id)"
            type="button"
            class="btn btn-outline-danger btn-xs"
          >
            allow
            <!-- <i class="fa fa-allow" aria-hidden="true"></i> -->
          </button>
          <br />
          <input
            v-if="row.item.role == 'Student'"
            class="input"
            v-model="row.item.units"
            type="text"
            @keydown="
              (e) => {
                updateUnitsAllowed(e, row.item._id);
              }
            "
            placeholder="Allowed units"
          />
        </div>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import axios from "axios";
import { parse } from "querystring";
import { getStage } from "../../functions";
export default {
  created() {
    this.getUsers();
  },
  data() {
    return {
      users: [],
      filteredUsers: [],
      modifiedUsers: [],
      stages: {},
      type: "",
      stage: "all",
      role: "0",
      confirmed: "all",
      registered: "all",
      allowed_units: {},
      headers: [
        {
          text: "Student name",
          align: "start",
          sortable: false,
          value: "fullname",
        },
        { text: "", align: "start", sortable: false, value: "email" },
        { text: "Role", align: "start", sortable: false, value: "role" },
        { text: "", align: "start", sortable: false, value: "phone" },
        { text: "  ", align: "start", sortable: false, value: "parentPhone" },
        { text: " ", align: "start", sortable: false, value: "address" },
        { text: " ", align: "start", sortable: false, value: "stage" },
        { text: " ", align: "start", sortable: false, value: "confirmed" },
        { text: " ", align: "start", sortable: false, value: "units" },
        { text: "Controls / Units", align: "start", sortable: false, value: "controls" },
      ],
    };
  },
  methods: {
    getUsers(type) {
      this.type = type;
      axios.get("/getUsers").then((res) => {
        const users = [...res.data.users].filter(
          (user) => user._id != localStorage.getItem("userId") && !user.admin
        );
        console.log(users);
        this.users = users;
        this.filteredUsers = users;
        this.filterUsers();
      });
    },
    remove(id) {
      const confirmed = confirm("  ");
      if (!confirmed) return;
      axios.get("/deleteUser/" + id).then((res) => {
        const index = this.users.findIndex((fu) => {
          return fu._id === id;
        });
        this.users.splice(index, 1);
        this.filterUsers();
      });
    },
    toggleRole(id) {
      axios.get("/toggleUserRole/" + id).then((res) => {
        const user = res.data.user;
        const index = this.users.findIndex((fu) => {
          return fu._id === user._id;
        });
        this.users[index] = user;
        this.filterUsers();
      });
    },
    confirmUser(id) {
      axios.get("/confirmUser/" + id).then((res) => {
        const user = res.data.user;
        const index = this.users.findIndex((fu) => {
          return fu._id === user._id;
        });
        this.users[index] = user;
        this.filterUsers();
      });
    },
    search(event) {
      this.filteredUsers = this.users.filter((user) => {
        return (
          user.fullname.toLowerCase().includes(event.target.value) ||
          user.phone.includes(event.target.value) ||
          // user.parentPhone.includes(event.target.value) ||
          user.address.toLowerCase().includes(event.target.value) ||
          user.email.toLowerCase().includes(event.target.value)
        );
      });
    },
    filterUsers() {
      this.filteredUsers = this.users.filter((user) => {
        let matched = false;
        if (this.stage !== "all") {
          if (user.stage == this.stage) matched = true;
          else return (matched = false);
        } else matched = true;

        if (this.role !== "all") {
          if (user.role == this.role) matched = true;
          else return (matched = false);
        } else matched = true;

        if (this.confirmed !== "all") {
          if (user.confirmed == this.confirmed) matched = true;
          else return (matched = false);
        } else matched = true;

        if (this.registered !== "all") {
          if (user.registered == this.registered) matched = true;
          else return (matched = false);
        } else matched = true;

        return matched;
      });
    },
    updateUnitsAllowed(e, id) {
      if (e.keyCode == "13") {
        const units = e.target.value.split(",").map((s) => {
          return parseInt(s);
        });
        axios.post("/updateAllowedUnits/" + id, { units }).then((res) => {
          if (res.data.done) {
            this.$store.dispatch("writemessage", "    ");
            const user = res.data.user;
            const index = this.filteredUsers.findIndex((fu) => {
              return fu._id === user._id;
            });
            this.filteredUsers[index] = user;
            // this.filteredUsers = [...this.filteredUsers];
          } else {
            this.$store.dispatch(
              "writemessage",
              "      "
            );
          }
        });
      }
    },
    allowRegister(id) {
      axios.get("/allowRegister/" + id).then((res) => {
        if (res.data.done) {
          this.$store.dispatch("writemessage", "   ");
          const index = this.users.findIndex((fu) => {
            return fu._id === id;
          });
          this.users[index] = res.data.user;
          this.filterUsers();
        }
      });
    },
    unconfirmAll() {
      const confirmed = confirm("      ");
      if (!confirmed) return;
      axios.get("/unconfirmAll").then((res) => {
        if (res.data.done) {
          this.users = this.users.map((user) => {
            return { ...user, confirmed: 0 };
          });
          this.filterUsers();
          this.$store.dispatch("writemessage", "    ");
        } else {
          this.$store.dispatch("writemessage", "  ");
        }
      });
    },
    allowAll() {
      const confirmed = confirm("      ");
      if (!confirmed) return;
      axios.get("/allowAll").then((res) => {
        if (res.data.done) {
          this.users = this.users.map((user) => {
            return { ...user, registered: false };
          });
          console.log(this.users);
          this.filterUsers();
          this.$store.dispatch("writemessage", "    ");
        } else {
          this.$store.dispatch("writemessage", "  ");
        }
      });
    },
  },
  watch: {
    filteredUsers(val) {
      this.modifiedUsers = val.map((user) => {
        return {
          ...user,
          role: user.role == 0 ? "Student" : "Admin",
          confirmed: user.confirmed == 0 ? "not confirmed" : "confirmed",
          // units: JSON.stringify(user.units),
          stage: getStage(user.stage),
        };
      });
    },
  },
};
</script>

<style lang="scss" scoped>
// @import "../../assets/styles/table.scss";
