  margin: 15px;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: right;
  align-items: center;
  width: 40%;
  @media (max-width: 600px) {
    width: 100%;
    justify-content: center;
  }
  i {
    position: absolute;
    right: 2%;
  }
}
